@mixin btn() {
    padding: 0.3rem 0.5rem;
    border-radius: 5px;
    font-size: 0.75rem;
  
    &:hover {
      box-shadow: var(--box-shadow);
    }
}
  
@mixin link() {
    color: var(--text-link-color);
    font-weight: bold;
  
    &:hover {
      text-decoration: underline;
    }
  }
  
@mixin card() {
    padding: 1rem;
    border-radius: 10px;
    background: var(--light-color);
    box-shadow: var(--box-shadow);
  }